

























import ut from '../../utils/index.js';
export default {
	data() {
		return {
			typeindex:0,
			swipeList: ['../../static/index/banner.jpg','../../static/index/banner.jpg'],
			indicatorDots: true,
			indicatorcolor: 'white',
			indicatoractivecolor:'#FEC200',
			autoplay: true,
			interval: 5000,
			duration: 1000,
			circular: true,
			server:[],
			classlist:[],
			clientServiceClasses:[],
			activeindex:0,
			static:""
		}
	},
	onLoad(opt) {
		this.static=ut.static;
		this.req_class(opt._id);
		ut.settitle(opt.title||'服务列表');
	},
	methods: {
		go_home_serverinf(id,name){
			wx.navigateTo({
				url: `../home/serverinf?_id=${id}&title=${name}`
			})
		},
		cg_typeindex(index){
			this.typeindex=index;
			this.req_classlist(this.server[index].id)
		},
		req_class(id){
			ut.request({
				data: {
					parentid:id
				},
				url: "service/class"
			}).then(data=>{
				this.server=data;
				return data
			}).then(data=>{
				if(data[0]){
					this.req_classlist(data[0].id)
				}
			})
		},
		req_classlist(id){
			ut.request({
				data: {
					parentid:id
				},
				url: "service/classlist"
			}).then(data=>{
				this.clientServiceClasses=data;
			})
		}
	}
}
